.gallery{
    .inner{
        position: relative;    
        article{
            width: 100%;
            height: auto;
            padding: 50px 0;
            border: 1px solid red;
        }    
        svg{
            // border: 1px solid red;
             font-size: 40px;
             color: $onblackbg;
             display: inline-block;
             left: 50%;
             margin-bottom: 30px;
         }
        
        h2{
            font: normal 25px/1 'arial';
            color: $whitebg;
            margin-bottom: 50px;
            text-align: center;
        }

        //검색박스
        >.search{
            border: 1px solid red;
            width: 100%;
            padding: 20px 0;
            margin: 0 auto;
            text-align: center;
            .searchBox{
                width: 300px;
                height: 30px;
                padding: 10px;
            }
            .searchBtn{
                width: 120px;
                height: 30px;
                border: none;
                margin: 0 20px;
                font: 14px/30px 'arial';
                background: $onblackbg;
                color: #fff;
                cursor: pointer;
            }            
        }
        #imgs{
            width: 100%;
            //로딩바
            .loading{
                border: 15px outset $onblackbg;
                width: 150px;
                height: 150px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                border-radius: 50%;
                animation: loading 2s linear infinite;    

                span{
                    color: $standard;
                    font: 18px/140px 'arial';
                    text-align: center;
                    display: block;
                }            
            }

            &::after{
                content: '';
                display: block;
                clear: both;
            }
            .item{
                border: 1px solid red;
                width: 20%;
                float: left;
                padding: 20px;

                .wrap{
                    width: 100%;
                  //  height: 100%;
                    .pic{
                        width: 100%;
                        border: 1px solid red;
                        overflow: hidden;
                        .img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                    .txt{
                        display: none;
                        border: 1px solid red;
                        width: 100%; 
                        color: $standard;
                        h3{
                            font: 16px/1.2 'arial';
                            word-break: break-all;
                        }
                    }
                }
            }
        }
    }
}

@keyframes loading {
    0% { transform: translate(-50%,-50%) rotate(0deg); }
    50%{ border-width: 0px; }
    100% { transform: translate(-50%,-50%) rotate(360deg); }
  }